.section {
  background: #ffffff;
  box-shadow: 0px 12px 24px rgba(87, 111, 172, 0.08);
  border-radius: 12px;
  padding: 24px;
}
.sectionHead {
  margin-bottom: 24px;
  display: flex;
  align-items: center;
}

.sectionTitle {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}
.sectionLabel {
  margin-top: 6px;
}
.sectionRight {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.sectionBody {
}
